import { scheme } from './common';

const initHttpButton = () => {
    const http11 = document.getElementById('http11');
    const http2 = document.getElementById('http2');

    alert(scheme);
    if (!http11 || !http2) {
        return;
    }

    if (scheme === 'http:') {
        http11.classList.add('uk-active');
        http2.classList.remove('uk-active');
    } else {
        http11.classList.remove('uk-active');
        http2.classList.add('uk-active');
    }
}
initHttpButton();